import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-list-person',
  templateUrl: './list-person.component.html',
  styleUrls: ['./list-person.component.css']
})
export class ListPersonComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {
  }

  arrPerson = [{ id: 1, name: 'htc', age: '19' }, { id: 2, name: 'htc', age: '12' }, { id: 3, name: 'asdczx', age: '34' }, { id: 4, name: 'dasc', age: '10' }];
  removeChildPerson(name: string){
    const index = this.arrPerson.findIndex(person =>person.name===name);
    this.arrPerson.splice(index,1);
  }
}
