import {Component} from '@angular/core';

@Component({
    templateUrl:'./word.component.html',
    selector: 'app-word',
    styleUrls:[`./word.component.css`]
})

export class WordComponent{
    en: string = 'Hello';
    vn: string = 'Xin Chao';
    imageUrl: string = 'https://www.gstatic.com/webp/gallery/1.jpg';
    forgot = false;

    toggleForgot(){
        this.forgot = !this.forgot;
    }
}