import { Component } from '@angular/core';
import { IpService } from './ip.service';

@Component({
    selector: 'app-ip',
    template: `
     <h3>ip {{ip}}</h3>
    `,
    providers: [IpService]
})
export class IpComponent {
    ip = '';
    constructor(private ipService: IpService) {
        this.ipService.getIp()
        .then(res => this.ip = res);
    }

}